#container{
    width:80%;
    margin:auto;
}

@media(min-width:320px){
    #body{
        margin-top: 100px;
        display: inline-block;
    }
    #left{
        width:100%;
    }
    #right{
        width:100%;
        font-size: 16px;
        text-align: left;
    }
    
}

@media(min-width:620px){
    #body{
        margin-top: 100px;
        display: flex;
    }
    #left{
        width:50%;
    }
    
    #right{
        width:50%;
        font-size: 16px;
        text-align: left;
    }

}

@media(min-width:1000px){
    #body{
        margin-top: 100px;
        display: flex;
    }
    #left{
        width:50%;
    }
    
    #right{
        width:50%;
        font-size: 16px;
        text-align: left;
    }

}





#left{
    font-size: 30px;
    text-align: left;
}

#left>p{
    font-size: 20px;
    line-height: 30px;
}

#right>p{
    margin-bottom:30px;
    margin-top: 30px;
}

#right>li{
    list-style:none;
    margin: 10px 0 ;
}

#right>li>i{
    color:red;
    margin-right: 10px;
    font-size:10px;
}