#container{
    width:80%;
    margin: auto;
}

body{
    background-size: cover;
    background-image:linear-gradient(#000000b0,#000000b0), url("../../bg.jpg");
    color:#fff;
    background-attachment:fixed;
    text-align: center;
    font-family: 'Noto Sans Nag Mundari', sans-serif;
    font-family: 'Raleway', sans-serif;
}

#heading>h2{
    font-size:48px;
    margin-top:100px;
    margin-bottom: 0px;
    font-weight:bolder;
    margin-left: -16px;

}

#heading>p{
    font-size:22px;
    margin-top: 5px;
    margin-bottom: 50px;
}
.time{
    display:flex;
    justify-content: center;
}

#days,#hours,#minutes,#second{
    background-color: rgba(76, 76, 75, 0.555);
    width: 100px;
    height:137px;
    margin:0 10px;
    font-weight:bolder;
    position: relative;
}

#days>h1,#hours>h1,#minutes>h1,#second>h1{
    font-size:42px;
}

@media(min-width:320px){
    #days::after{
        content: "";
        position: absolute;
        width: 91%;
        border: 3px solid red;
        top: 0;
        left: 0;
    }

    #hours::after{
        content: "";
        position: absolute;
        width: 87%;
        border: 3px solid red;
        top: 0;
        left: 0;
    }
    
    #minutes::after{
        content: "";
        position: absolute;
        width: 89%;
        border: 3px solid red;
        top: 0;
        left: 0;
    }
    
    #second::after{
        content: "";
        position: absolute;
        width: 90%;
        border: 3px solid red;
        top: 0;
        left: 0;
    }
    
}

@media(min-width:620px){
    #days::after{
        content: "";
        position: absolute;
        width: 91%;
        border: 3px solid red;
