#sub{
    position: relative;
}

#sub>p{
    margin-top: 90px;
    font-size:22px;
    font-weight:bolder;
}



@media(min-width:320px){
    #sub>a{
        display: inline-block;
        text-decoration: none;
        color: #fff;
        font-size: 18px;
        font-weight: bold;
        background-color: red;
        position: absolute;
        top: 100%;
        left: 25%;
        padding: 9.5px;
        width: 100px;
    }
    #sub>input{
        width:260px;
        height:35px;
        right: 4%;
        position: inherit;
    }
}

