#container{
    width:80%;
    margin:auto;
    text-align: center;
    margin-top: 80px;
}

form{
    background-color: #8382821e;
    text-align: center;
}

#form>h2{
    font-size: 38px;
}

@media(min-width:320px){
    #detail{
        display:flex;
        flex-wrap: wrap;
        text-align:left;
        justify-content: space-evenly;
        margin-bottom: 30px;
    }
    #details{
        width: 100%;
        display:flex;
        align-items: center;
    }
    
}

@media(min-width:620px){
    #detail{
        display:flex;
        flex-wrap: wrap;
        text-align:left;
        justify-content: space-evenly;
        margin-bottom: 30px;
    }
    #details{
        width: 50%;
        display:flex;
        align-items: center;
    }
}

@media(min-width:1000px){
    #detail{
        display:flex;
        text-align:left;
        justify-content: space-evenly;
        margin-bottom: 30px;
    }
    #details{
        width: 33.33%;
        display:flex;
        align-items: center;
    }
}




#details>i{
    margin-right: 30px;
    font-size:26px;
    width:50px;
    height:50px;
    border-radius: 50%;
    background-color: #8382823e;
    justify-content: center;
    align-items: center;
    display: flex;
}

#one{
    width:100%;
    display: flex;
    justify-content: center;
}

#one>input{
    width:38.5%;
    background-color: #8382823e;
    border:none;
    height:20px;
    margin: 10px;
    padding: 10px;
}

#two{
    padding: 20px;
}

#two>input{
    width:84%;
    background-color: #8382823e;
    border:none;
    height:20px;
    margin-bottom: 20px;
    padding: 10px;
}

#two>textarea{
    width:84%;
    background-color:#8382823e;
    border:none;
    height:50px;
    margin: 10px;
    padding: 10px;
}

#two>a{
    text-decoration: none;
    display:flex;
    color:#fff;
    background-color: red;
    justify-content: center;
    align-items: center;
    width:150px;
    height:40px;
    margin: auto;
    border-radius: 5px;
}
#design>span{
    color:red;
}

#footer{
    margin-top:100px;
    margin-bottom:30px;
    font-size:14px;
}